const header = document.querySelector("#header");

window.onscroll = function () {
  var currentScrollPos = window.pageYOffset;
  // 20 is an arbitrary number here, just to make you think if you need the prevScrollpos variable:
  if (currentScrollPos > 100) {
    // I am using 'display' instead of 'top':
    header.style.top = "-20%";
  } else {
    header.style.top = "0";
  }
};

const track = document.querySelector(".carousel_track");
const slides = Array.from(track.children);
const nextButton = document.querySelector("#carousel__button_right");
const prevButton = document.querySelector("#carousel__button_left");

let _index = 1;
let slideWidth = slides[0].getBoundingClientRect().width;

slides.forEach((slide, index) => {
  slide.style.left = slideWidth * index + "px";
});

const moveToSlide = (track, currentSlide, targetSlide) => {
  console.log("moved");
  track.style.transform = "translateX(-" + targetSlide.style.left + ")";
  currentSlide.classList.remove("current-slide");
  targetSlide.classList.add("current-slide");
};

nextButton.addEventListener("click", () => {
  const currentSlide = document.querySelector(".current-slide");
  const nextSlide = currentSlide.nextElementSibling;
  _index++;
  moveToSlide(track, currentSlide, nextSlide);

  if (_index == 3) {
    nextButton.classList.add("is-hidden");
    prevButton.classList.remove("is-hidden");
  }
});

prevButton.addEventListener("click", () => {
  const currentSlide = document.querySelector(".current-slide");
  const prevSlide = currentSlide.previousElementSibling;
  _index--;
  moveToSlide(track, currentSlide, prevSlide);
  if (_index == 1) {
    prevButton.classList.add("is-hidden");
    nextButton.classList.remove("is-hidden");
  }
});

setInterval(() => {
  if (nextButton.classList.contains("is-hidden")) {
    prevButton.click();
  } else {
    nextButton.click();
  }
}, 5000);

const options = {
  root: null, // use the document's viewport as the container
  rootMargin: "0px", // % or px - offsets added to each side of the intersection
  threshold: 0.3, // percentage of the target element which is visible
};

let callback = (entries) => {
  entries.forEach((entry) => {
    if (entry.isIntersecting) {
      entry.target.style.opacity = 1;
      entry.target.style.transform = "translateY(0)";
    }
  });
};

let Fadecallback = (entries) => {
  entries.forEach((entry) => {
    if (entry.isIntersecting) {
      entry.target.style.opacity = 1;
      entry.target.style.transform = "translateX(0)";
    } else {
    }
  });
};

let observer = new IntersectionObserver(callback, options);
document.querySelectorAll(".fade-up").forEach((box) => {
  observer.observe(box);
});

setTimeout(() => {
  const card = document.getElementById("card");
  card.style.opacity = 1;
  card.style.transform = "translateY(0)";
}, 1800);
